
import 'swiper/dist/css/swiper.css';
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import { isScrollBottom } from '@/utils/comm.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { swiper, swiperSlide } from 'vue-awesome-swiper';
export default {
components: {
XHeader,
swiper,
swiperSlide
},
data() {
return {
swiperOption: {
autoplay: false,
autoHeight: true,
on: {
slideChangeTransitionStart: () =>{
this.currentTab = this.$refs.mySwiper.swiper.activeIndex;
}
}
},
onelistInfo: "",
twolistInfo: "",
winWidth: 0,
winHeight: 0,

currentTab: 0,
oneupLoading: false,
oneupLoadingComplete: false,
twoupLoading: false,
twoupLoadingComplete: false,
onepage: 1,
twopage: 1,
total_amout: "0.00",
avatar: "",
isNull_1: false,
isNull_0: false,
}
},
created() {
this.showoneData();
this.showTwoDate();
},
mounted() {
this.$nextTick(()=>{
isScrollBottom(this.onBottom);
})
},
destroyed () {
window.onscroll = null;
},
methods: {
onBottom(){
if (this.currentTab == 0) {
if (!this.oneupLoadingComplete) {
this.showoneData()
}
} else {
if (!this.twoupLoadingComplete) {
this.showTwoDate()
}
}
},
swichNav(current){
if(this.currentTab != current){
this.currentTab = current;
}else{
return;
}
this.$nextTick(()=>{
this.$refs.mySwiper.swiper.slideTo(current);
})
},
async showoneData(){
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.onepage,
type: 0
}
const [err, res] = await api.refundlist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if (this.onelistInfo == "") {
if (res.data.list == "") {
this.isNull_0 = true;
} else {
this.onelistInfo = res.data.list;
this.isNull_0 = false;
this.onepage = this.onepage + 1;
this.oneupLoading = false;
}
} else {
this.onelistInfo = that.onelistInfo.concat(res.data.list);
if (res.data.list == "") {
this.oneupLoadingComplete = true;
this.oneupLoading = false;
} else {
this.onelistInfo = this.onelistInfo;
this.onepage = this.onepage + 1;
this.oneupLoading = false;
}
}
}
},
async showTwoDate(){
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.twopage,
type: 1
}
const [err, res] = await api.refundlist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if (that.twolistInfo == "") {
if (res.data.list == "") {
this.isNull_1 = true;
} else {
this.twolistInfo = res.data.list;
this.twopage = this.twopage + 1;
this.isNull_1 = false;
}
} else {
this.twolistInfo = this.twolistInfo.concat(res.data.list);
if (res.data.list != "") {
this.twopage = this.twopage + 1;
this.twoupLoading = false;
} else {
this.twoupLoadingComplete = true;
this.twoupLoading = false;
}
}
}
},
openInfo(refund_id){
this.$router.push({
path: '/centerFull/orderFull/afterlistInfo',
query: {
refund_id
}
})
},
buyagain(){

},
goWheremoney(refund_id){
this.$router.push({
path: '/centerFull/orderFull/aftermoneyWhere',
query: {
refund_id
}
})
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



